.accordion-button:not(.collapsed) {
    color: #ffffff;
    background-color: #081b24;
    border-color:#081b24 ;
    box-shadow: inset 0 -1px 0 rgb(0 0 0 / 13%)}


    .accordion-button:focus {
        z-index: 3;
        border-color: #1d1d1e;
        outline: 0;
        box-shadow: none
    }